<template>
	<div class="app-container">
		<p>
			<el-col >
				选择时间段：
				<el-date-picker v-model="dateInput" type="daterange" align="right" placeholder="选择日期范围" :picker-options="pickerOptions2" @change="dateChange">
				</el-date-picker>
	
			</el-col>
		</p>
		<div>
			 <el-card class="box-card" :body-style="{ padding: '0 20px' }" > 
				<div slot="header" class="clearfix" v-show="isVisiable" >
					<span style="line-height: 0px;font-size: 15px;color:#333">流量包订购情况</span>
				</div>
				 <div class="chart-container" v-show="isVisiable" v-loading="isLoad">
					<div id="main" style="width: 100%;">
						<div class="line1">{{line1}}</div>
                          <div class="line2">{{line2}}</div>
                          <div class="line3" style="margin-left:7px">{{line3}}</div>
					</div>
				</div> 
			 </el-card> 
			<div class="hidden-content" v-if="isShowDetail">
				<el-table :data="tableData" border style="width: 100%" v-loading="isLoad">
					<el-table-column align="center" prop="number" label="接入号码" width="140">
					</el-table-column>
					<el-table-column align="center" prop="iccid" label="ICCID" width="200">
					</el-table-column>
					<el-table-column align="center" prop="orderId" label="订单号">
					</el-table-column>
					<el-table-column align="center" prop="time" label="交易时间" width="120">
					</el-table-column>
					<el-table-column align="center" prop="content" width="210" label="订单内容" :formatter="orderContent">
					</el-table-column>
					<el-table-column align="center" prop="price" label="金额(元)" width="100">
					</el-table-column>
				</el-table>
				<div style="text-align:center;margin: 20px auto;">
					<el-pagination style="display: inline-block" :page-size="10" @current-change="pageChange" :current-page.sync="currentPage" layout="prev, pager, next, jumper" :total="tableCount">
					</el-pagination>
				</div>
			</div>
			<!-- <div class="card-footer">详情</div> -->
		</div>
	</div>
</template>

<script>
/* eslint-disable quotes,indent,keyword-spacing,spaced-comment,no-var */
// 引入 ECharts 主模块
// const echarts = require('echarts');
import {
	getListManufacturerGeneral,
	getListIotCardByMatch,
	giveManufacturerAuthority
} from 'api/admin/usual';
import {
	getOrderStatisticsByDate,
	getListPayOrderByDate
} from 'api/admin/orderStatistics';
export default {
	data() {
		return {
			pickerOptions2: {
				shortcuts: [{
					text: '最近一周',
					onClick(picker) {
						const end = new Date();
						const start = new Date();
						start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
						picker.$emit('pick', [start, end]);
					}
				}, {
					text: '最近一个月',
					onClick(picker) {
						const end = new Date();
						const start = new Date();
						start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
						picker.$emit('pick', [start, end]);
					}
				}, {
					text: '最近三个月',
					onClick(picker) {
						const end = new Date();
						const start = new Date();
						start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
						picker.$emit('pick', [start, end]);
					}
				}]
			},
			options: [],
			dateInput: ['2017-03-01', '2017-12-12'],
			mainChart: {},
			value: '',
			tableData: [],
			tableCount: 0,
			currentPage: 1,
			currentDate: ['20170301', '20171212'],
			isShowDetail: true,
			isVisiable:true,
			isLoad:true,
			line1:'',
			line2:"分别为:",
			line3:''
		}
	},
	watch: {
		options() {
			this.value = this.options[0].mid
		}
	},
	mounted() {
		// this.mainChart = echarts.init(document.getElementById('main'));
		this.ListPayOrderByDate(1, 10,this.currentDate[0], this.currentDate[1])

	},
	methods: {
		pageChange(page) {
                 this.ListPayOrderByDate(page, 10, this.currentDate[0], this.currentDate[1])
		},
		dateChange(date) {
			this.currentDate=[date.split(" - ")[0].split("-").join(''),date.split(" - ")[1].split("-").join('')]
			this.ListPayOrderByDate(1, 10, this.currentDate[0], this.currentDate[1])
		},
		// _initMainChart(options) {
		// 	var option = {
		// 		title: {
		// 			text: '',
		// 			x: 'left'
		// 		},
		// 		tooltip: {
		// 			trigger: 'item',
		// 			formatter: "{a} <br/>{b} : {c} ({d}%)"
		// 		},
		// 		legend: {
		// 			orient: 'vertical',
		// 			left: 'right',
		// 			data: options.legend
		// 			//						data: ['物联网月加餐包4元30MB', '邮件营销', '联盟广告', '视频广告', '搜索引擎'],
		// 		},
		// 		series: [{
		// 			name: '订购情况',
		// 			type: 'pie',
		// 			radius: '55%',
		// 			center: ['50%', '60%'],
		// 			data: options.data,
		// 			itemStyle: {
		// 				emphasis: {
		// 					shadowBlur: 10,
		// 					shadowOffsetX: 0,
		// 					shadowColor: 'rgba(0, 0, 0, 0.5)'
		// 				}
		// 			}
		// 		}]
		// 	};
		// 	this.mainChart.setOption(option);
		// },
		handleOptions(options) {
			var obj = {};
			obj.legend = [];
			obj.data = [];
			for (var i in options.packageList) {
				var packObj = {};
				packObj.name = options.packageList[i].packageName;
				packObj.value = parseInt(options.packageList[i].packageCount);
				obj.legend.push(options.packageList[i].packageName);
				obj.data.push(packObj);
			}
			return obj;
		},
		orderContent(val) {
			return val.content.replace(/\（数据\）/g, '').replace(/非定向/g, '').split('（')[0];
		},
		OrderStatisticsByDate(startDate, endDate) {
			this.isLoad=true
			getOrderStatisticsByDate({ startDate, endDate })
				.then(res => {
					this.isLoad=false
					console.log("res",res);
					// console.log(this.handleOptions(res));
					// this._initMainChart(this.handleOptions(res));
					 this.line1 = "在该时段内，共有" + res.userCount + "位用户进行充值，充值总金额为" + res.priceTotal + "元,充值流量叠加包数量总计" + res.packageCount + "个。"
					let that=this
					this.line3='';
				for(let a of res.packageList.keys()){
					console.log(res.packageList.length);
					(a % 2 == 0 && a != 0)?that.line3 += '<br>' : '';
					that.line3 += "[" + res.packageList[a].packageName + ":" + res.packageList[a].packageCount + "个]";
                    (a + 1 == res.packageList.length) ? (that.line3  += " 。") : (that.line3 += ',');
				}
				this.line3=that.line3
				console.log(this.line3)
				});
		},
		ListPayOrderByDate(page, num, startDate, endDate) {
			this.isLoad=true
			getListPayOrderByDate({ page, num, startDate, endDate })
				.then(res => {
					
					if (res.msg == 0) {
						this.tableData = res.payOrderList;
						if(page===1){
							this.tableCount = res.count
							this.OrderStatisticsByDate(startDate,endDate)
						}else{
							this.isLoad=false
						}
					} else {
						this.isVisiable=false
						this.isLoad=false
						this.tableData = [];
						this.tableCount = 0;
					}
					(res.msg != 0 && res.msg != 1) ? this.$message.error(backCN(res.code)) : ''
				}).catch(err => {
					this.isLoad = false
					this.$message.error(backCN(err.status))
				});
		}
	}
};
</script>

<style scoped>
.app-container {
	background: #f5f5f5;
}

.box-card {
	border-top-left-radius: 4px;
	border-top-right-radius: 4px;
	border-bottom-right-radius: 0px;
	border-top-left-radius: 0px;
}

.hidden-content {
	padding: 20px;
	border-top: 1px solid #eaeefb;
	border-bottom: 1px solid #D1DBE5;
	border-left: 1px solid #D1DBE5;
	border-right: 1px solid #D1DBE5;
	background-color: #f5f5f5;
	box-sizing: border-box;
	text-align: center;
	margin-top: -1px;
	box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04);
	transition: .2s;
	position: relative;
}

.card-footer {
	border-top: 1px solid #eaeefb;
	border-bottom: 1px solid #D1DBE5;
	border-left: 1px solid #D1DBE5;
	border-right: 1px solid #D1DBE5;
	height: 36px;
	box-sizing: border-box;
	background-color: #fff;
	border-bottom-left-radius: 4px;
	border-bottom-right-radius: 4px;
	text-align: center;
	margin-top: -1px;
	box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04);
	color: #d3dce6;
	cursor: pointer;
	transition: .2s;
	position: relative;
}
</style>


